import { Duration } from "../model/model";

class DateUtils {
    private static dayMills: number = 24 * 60 * 60 * 1000
    public static dayOffset: number = 6

    // 获得今天某个小时的时间戳
    public static getCurrentDayStamp(hour: number): number {
        let today = new Date();
        today.setHours(hour, 0, 0, 0);
        return today.getTime();
    }

    // 获得某个时间戳那天某个小时的时间戳
    public static getSomeDayStamp(someStamp: number, hour: number): number {
        let day = new Date(someStamp)
        day.setHours(hour, 0, 0, 0)
        return day.getTime();
    }

    // 获得某天偏移某个小时后的dur
    // 暂时 offsetHour 只支持 0-24 之间的数
    public static getOffsetedDayDur(dayZeroTime: number, offsetHour: number): Duration {
        let day = new Date(dayZeroTime)
        day.setHours(offsetHour, 0, 0, 0)

        let result = new Duration()
        result.init(day.getTime())
        result.end = day.getTime() + DateUtils.dayMills
        return result
    }

    // 返回某个dur覆盖到的所有天的0点时间戳
    public static getDurationCoveredDays(dur: Duration): number[]{
        let startDay = DateUtils.getSomeDayStamp(dur.start, 0)
        const endDay = DateUtils.getSomeDayStamp(dur.end, 0)
        const result = []
        result.push(startDay)
        while (startDay < endDay) {
            startDay += DateUtils.dayMills
            result.push(startDay)
        }
        result.push(endDay)
        return result
    }

    //-------------------- 以下业务相关 --------------------

    /**
     * 获得今天（是否当天按 dayOffset 判断） offset 某些天的 0 点时间戳
     */
    public static getMyCurrentDayStamp(offsetDay: number, dayOffset: number): number {
        let now = new Date().getTime()
        // 今天6点
        let todayOffset = DateUtils.getCurrentDayStamp(dayOffset)

        if (now > todayOffset) {
            return DateUtils.getCurrentDayStamp(0) + DateUtils.dayMills * offsetDay
        } else {
            return DateUtils.getCurrentDayStamp(0) - DateUtils.dayMills + DateUtils.dayMills * offsetDay
        }
    }

    /**
     * 获得今天（是否当天按6点判断） offset 某些天的 dur，这个某些天本身也 offset 6
     */
    public static getMyCurrentDayDur(offsetDay: number): Duration {
        return DateUtils.getOffsetedDayDur(DateUtils.getMyCurrentDayStamp(offsetDay, DateUtils.dayOffset), DateUtils.dayOffset)
    }

    /**
     * 获得今天（是否当天按0点判断） offset 某些天的 dur, 这个是0
     */
    public static getMyCurrentDayDur2(offsetDay: number): Duration {
        return DateUtils.getOffsetedDayDur(DateUtils.getMyCurrentDayStamp(offsetDay, 0), 0)
    }

    public static isSameDay(time1: number, time2: number): boolean {
        return new Date(time1).getDay() ===  new Date(time2).getDay()
    }


}

export default DateUtils